/*Blog page */
#page-header.blog-header{
 
    background-image: url(img/banner/b19.jpg);
 }
 #blog{
    padding : 150px 150px 0 150px;

 }
  #blog .blog-box{
    display: flex;
    align-items: center;
    width: 100%;
    position: relative;
    padding-bottom: 90px;
  }

  #blog .blog-img{
    width: 50%;
    margin-right: 40px;

  }
  #blog img{
    width: 100%;
    height: 300px;
    object-fit: cover;
  }
  #blog .blog-details{
    width: 50%;
  }

  #blog .blog-details a {
    text-decoration: none;
    font-size: 11px;
    color: #000;
    font-weight: 700;
    position: relative;
    transition: 0.3s;
  }
  #blog .blog-details p{
    color: #222;
  }
  #blog .blog-details a::after{
      content:"";
      width: 50px;
      height:1px;
      background-color: #000;
      position:absolute;
      top: 4px;
      right: -60px;

  }
  #blog .blog-details a:hover{
    color:#088178;
  }
  #blog .blog-details a:hover::after{
    background-color: #088178;
  }
 
  #blog .blog-box h1{
    position: absolute;
    top:-40px;
    left:0;
    font-size: 70px;
    font-weight: 700;
    color: #b4b8be;
    z-index: -9;


  } 



